*{padding:0;
    margin:0;
}
.header_container
{
    background-color:#003D32;
    display: flex;
    gap:10px;
    align-items: white;
    height: 70px;
}
.logo_container 
{
    display: flex;
    align-items: center;
    justify-content: center;
    max-width: 250px;
}
.logo_container img
{
    width: 80px;
    padding-top:5px;
    margin-left: 20px;
    vertical-align: middle;
}
p{
   
    color:white;
    
}
.delivery {
display :flex;
justify-content: center;
align-items: center;
gap: 3px;
font-weight: bold;
padding: 5px;
margin-left: 5px;
}

.delivery p{
    font-size: 10px;
}
 .search
{
    
    margin-top: 14px;
    margin-bottom: 14px;
    display: flex;
   
    align-items: center;
    justify-content: center;
    border-radius: 10px;
    overflow: hidden;
    width: 60%;
    /* flex:1;  */
}
.search select ,
.search input{
    padding: 10px;
    font-weight: bold;
    outline: 1px solid white;
}
.search select{
    border: none;
}
.search input 
{
    border:1px solid white;
    width: 100% ;
    margin-right:-2px;
}
.search svg{
    padding: 7px;
    background: #febd69;
    color:#1a1a1a
}
.search svg:hover{
    background-color: #d49644;

} 
.search:focus-within{
border: 2px solid rgb(235,161,173);
}
.order_container{
    display: flex;
    align-items: center;
    justify-content: center;
    max-width: 450px;
    gap: 15PX;
}
.order_container a{
    text-decoration:none;
    color: white;
}
.order_container p
{
    font-size: 10px;
}
.language
{
    width: 80px;
    display: flex;
    padding: 10px;
}
.language select{
    background-color: #003D32;
    border: none;
    color:white;
}
.language img{
    width: 40%;
}
.ordersbg{
    background-color:#003D32;
    border: none;
    color:white;
}
 .cart{
    position:relative;
    padding-left:10px;
    margin-right: -30px;
}
.cart span{
    position:absolute;
    margin-left: -15px;
    font-weight: bold;
    top: -3px;
    
    font-size: 14px;
    color:orange;
} 
.order_container a:hover,
.delivery:hover,
.cart:hover,
.language:hover,
.logo_container a:hover,
.logo_container ul li :hover{
    border:1px solid white;
    border-radius: 3px;
}
.lower_container ul{
    display: flex;
    list-style: none;
    gap:5px;
}
.lower_container ul li{
    padding:8px;
    color:white;

}
.lower_container ul li:first-child{
    display:flex;
    gap: 2px;
    align-items: center;
}
.lower_background
{
    background-color: #085749;
}
.fixed{
    position: sticky;
    top:0;
    z-index: 100;
}
.p{
    color:white;
}
@media screen and (max-width:576px
)
{
    .header_container
    {
        display:flex;
        flex-direction: column!important;
        height:auto;
    }
    .lower_container ul li:not(:first-child)
    {
        display:none;
    }
    .language{
        display: none;
    
    }
}